@keyframes timelimitProgress {
  0% {
    transform: translate3d(0%, 0, 0);
  }
  50% {
    transform: translate3d(50%, 0, 0);
  }
  75% {
    transform: translate3d(75%, 0, 0);
  }
  100% {
    transform: translate3d(100%, 0, 0);
  }
}
@keyframes feedToast {
  from {
    display: none;
    opacity: 0;
  }
  1% {
    display: flex;
    opacity: 0;
  }
  51% {
    opacity: 1;
  }
  to {
    display: none;
    opacity: 0;
  }
}
