/* デフォルトの outline を消す */
:focus {
  outline: none;
}

body,
html {
  min-height: 100vh;
  background-color: #fafbfd;
}

body {
  margin: 0;
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

button:disabled {
  cursor: auto;
}

@media screen and (min-width: 769px) {
  ._content {
    position: relative;
    flex: 1 1 auto;
    background-color: #f5f6fa;
  }
}

._content--smaller--white {
  padding: 40px 10px;
  background-color: #fff;
}

._content--smaller--blue {
  padding: 40px 10px;
  background-color: #f5f6fa;
}

._content--navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 16px;
}

.main-menu {
  flex: 0 0 240px;
  height: calc(100vh - 74px);
  border-right: #dcdde6 1px solid;
}

.main-menu__content {
  width: 100%;
  padding: 30px 30px 28px;
  overflow-y: scroll;
}

._wrapper {
  min-height: 100vh;
}

._wrapper ._wrapper__content {
  display: flex;
}

@keyframes timelimitProgress {
  0% {
    transform: translate3d(0%, 0, 0);
  }
  50% {
    transform: translate3d(50%, 0, 0);
  }
  75% {
    transform: translate3d(75%, 0, 0);
  }
  100% {
    transform: translate3d(100%, 0, 0);
  }
}

@keyframes feedToast {
  from {
    display: none;
    opacity: 0;
  }
  1% {
    display: flex;
    opacity: 0;
  }
  51% {
    opacity: 1;
  }
  to {
    display: none;
    opacity: 0;
  }
}
